íses se basa en la producción de plantas medicinales. Uno de los mejores ejemplos son en países como la India y el suroeste asiático. "}})]),_c('div',{staticClass:"tarjeta BGR04 p-4",attrs:{"titulo":"Farmacoetimología"}},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img18.svg"),"alt":"Esquema de la Farmacoetimología, la cual estudia los diferentes nombres de las drogas en los diferentes pueblos, de la misma o distinta lengua; sin embargo. para no ir muy lejos, según la encuesta nacional de plantas medicinales y aromáticas en Colombia realizada por el Instituto Alexander Von Humbolt se presentan conflictos, tanto con los nombres comunes como con los nombres científicos, en cuanto a las especies manejadas por los laboratorios naturistas.<br>Como ejemplos se tienen:<br>La altamisa (Ambrosia cumanensis), conocida también con el nombre de artemisa, diferente de la especie Arthemisia absinthium L. conocida con el nombre común de Ajenjo, y completamente diferente a la especie anterior, aunque de la misma familia taxonómica (Compositae). Entonces, si se reporta la especie como artemisa no se reconoce de cuál especie se está hablando, si del ajenjo (Artemisia absinthium L.) o de la Altamisa (Ambrosia cumanensis).  <br>Las rutas de esta rama son:<br>La asociación de un mismo nombre vulgar a varias especies vegetales y viceversa, puede acarrear problemas sanitarios y también, posiblemente, creencias erróneas sobre la eficacia de un remedio. Se recomienda que ante una consulta sanitaria el paciente indique a su médico si toma infusiones u otros preparados vegetales, especialmente en el caso de tratarse de especies autóctonas utilizadas como remedio en medicina popular, ya que, como ejemplo, es muy diferente tomar “tila” procedente de Crataegus monogyna."}})]),_c('div',{staticClass:"tarjeta BGR04 p-4",attrs:{"titulo":"Farmacoemporia"}},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img19.svg"),"alt":"Esquema de la Farmacoemporia, la cual estudia el comercio, los puertos y todas las rutas que se utilizan para comercializar las plantas. Sus formas de adquirirlas son por:<br>Compra de grandes cantidades de droga a través de agentes extranjeros.<br>Aspectos a temer en cuenta:<br>Solicitar muestras por adelantado.<br>Solicitar certificados de análisis.<br>Existen productos caros que son susceptibles  de adulteración (azafrán, opio, cáñamo).<br>Comerciantes o agentes en la modalidad por consignación."}})]),_c('div',{staticClass:"tarjeta BGR04 p-4",attrs:{"titulo":"Farmacodiascomia"}},[_c('p',[_vm._v("Estudia los empaques y los embalajes de las drogas naturales. Los recipientes más adecuados suelen ser los recipientes metálicos; pero también los de vidrio, los cuales son más utilizados.")]),_c('p',[_vm._v("Los recipientes de madera, tela o plástico no son recomendables porque suelen ser relativamente permeables al aire y a los agentes externos. Las esencias deben de conservarse en envases herméticos totalmente llenos y en un lugar frío y oscuro.")]),_c('p',[_vm._v("Observaciones similares son aplicables a los aceites fijos, especialmente al aceite de hígado de bacalao, en este último caso, el aire del recipiente se reemplaza por un gas inerte.")]),_c('p',[_vm._v("Las preparaciones farmacéuticas de Digitalis purpúrea y Digitales lanata deben estar en recipientes herméticamente cerrados protegidos de la luz, el contenido de humedad no debe de sobrepasar el 6% para así evitar la pérdida de principios activos.")]),_c('p',{staticClass:"mb-0"},[_vm._v("Otro ejemplo relacionado con cuidados en la presentación farmacéutica es con relación a la pilocarpina. la cual se puede convertir en un isómero denominado isopto carpina con pérdida potencial de la actividad.")])])]),_c('p',{staticClass:"text-center",attrs:{"data-aos":"fade-right"}},[_vm._v("Las ramas de la farmacognosia son muy importantes; sin embargo, se enfrentan al reto del rápido desarrollo de otros sectores que han frenado su desarrollo.")])],1)],1)
